<template>
    <h1>Pokemon Page {{ id }}</h1>
    <h2>Cusquisiban Alfaro Raquel</h2>
    <h3>V Ciclo</h3>
    <div v-if="pokemon">
    <img :src="pokemon.sprites.front_default" :alt="pokemon.name" />
    </div>
    
</template>

<script>
export default {
    data() {
        return {
            pokemon: null,
        };
    },
    props: {
        id: {
            type: Number,
            required: true,
        },
    },
    methods : {
        async getPokemon() {
            try {
           const resp = await fetch(
            `https://pokeapi.co/api/v2/pokemon/${this.id}`
            ).then((response) => response.json());
            console.log(resp);
            this.pokemon = resp
        } catch {
            this.$router.push("/");
            console.log("Informacion no disponible");
        }
        },
    },
    created() { 
        this.getPokemon();
    },
    watch: {
        id() {
          this.getPokemon();
        },
    },
};
</script>